<template>
  <div class="index">
    <div class="btn-oper">
      <div @click="showDialog('culture')" class="btn-submit mr15">导入文化成绩</div>
      <div @click="showDialog('unculture')" class="btn-submit mr15">导入非文化成绩</div>
      <div v-show="form.analysisType === 'single'" @click="$exportDetail('/export/exportJuniorSingleCourseAnalysisData.do', impForm)" class="btn-submit">导出</div>
      <div v-show="form.analysisType === 'levelA'" @click="$exportDetail('/export/exportJuniorCourseLevelAStatData.do', impForm)" class="btn-submit">导出</div>
      <div v-show="form.analysisType === 'levelB'" @click="$exportDetail('/export/queryScoreLevelBConvertStatData.do ', impForm)" class="btn-submit">导出</div>
      <div v-show="form.analysisType === 'whole'" @click="$exportDetail('/export/queryClassEntireStatData.do', impForm)" class="btn-submit">导出</div>
    </div>
    <div class="search-box">
      <div class="item">
        <span class="text">考试批次</span>
        <el-cascader @change="changeBatch" class="cascader" v-model="batchModel"
                     :options="courseCascadeList"></el-cascader>
      </div>
      <div class="item">
        <span class="text">分析表类型</span>
        <el-select @change="changeAnalysisType()" v-model="form.analysisType">
          <el-option label="单科情况分析" value="single"></el-option>
          <el-option label="班内各科A等人数对比" value="levelA"></el-option>
          <el-option label="各科B等生转化率分析" value="levelB"></el-option>
          <el-option label="班级整体情况分析" value="whole"></el-option>
        </el-select>
      </div>
      <div class="item" v-show="form.analysisType === 'single'">
        <span class="text">科目</span>
        <el-select @change="getDetail()" default-first-option filterable v-model="form.courseId">
          <el-option v-for="item in courseList" :key="item.id"
                     :label="item.name" :value="item.id"
          ></el-option>
        </el-select>
      </div>
    </div>
    <div class="table-box">
      <div v-html="tableData"></div>
      <div class="no-data" v-if="tableData && tableData.indexOf('<table') === -1">暂无数据</div>
      <div v-show="!tableData" class="no-data">暂无数据</div>
    </div>
    <el-dialog :title="dialogTitle" class="dialog" :visible.sync="dialog" width="540px">
      <div class="item">
        <span class="title imp">考试批次</span>
        <el-cascader
            class="input"
            clearable
            v-model="impBatchList"
            :options="courseCascadeList"
        ></el-cascader>
      </div>
      <div class="item">
        <span class="title imp">上传文件</span>
        <el-input type="file" class="input file-input" ref="upload"
                  accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                  placeholder="请选择"></el-input>
        <a v-show="dialogTitle === '导入文化数据'" download href="../gy-files/template/初高中文化成绩导入模板.xls"
           class="f-small main-c ml5">下载模板</a>
        <a v-show="dialogTitle === '导入非文化成绩'" download href="../gy-files/template/非文化成绩导入模板.xlsx"
           class="f-small main-c ml5">下载模板</a>
      </div>
      <div class="btns">
        <div @click="impFun()" class="btn-submit btn-upload mr15">确认</div>
        <div @click="dialog = false" class="btn-clear">取消</div>
      </div>
    </el-dialog>
  </div>

</template>
<script>
  import mixins from '../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail, mixins.$spliceHTML],
    data() {
      return {
        // 科目列表
        courseList: [],
        // 选择的考试批次
        batchModel: [],
        // 查询条件
        form: {
          termId: null,
          gradeId: null,
          examBatchId: null,
          courseId: null,
          analysisType: 'single',
        },
        // 表格数据
        tableData: '',
        // 查询表格的接口
        searchUrl: '/score/queryJuniorSingleCourseStatData.do',
        dialog: false,
        // 导入时选择的考试批次
        impBatchList: [],
        // 学期级联列表
        courseCascadeList: [],
        dialogTitle: ''
      }
    },
    methods: {
      // 展现弹窗
      showDialog(type) {
        this.dialogTitle = type === 'unculture' ? '导入非文化成绩' : '导入文化数据'
        this.dialog = true
      },
      // 获取学科
      async getSubject() {
        this.courseList = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do',
        })
        this.form.courseId = this.courseList[0].id
      },
      // 获取学期 年级 考试批次级联
      async getCourseCascade() {
        this.courseCascadeList = await this.$ajax({
          url: '/score/queryTermGradeExamCascade.do'
        })
        let arr = this.courseCascadeList
        this.courseCascadeList = []
        arr.forEach((e, i) => {
          this.courseCascadeList.push({
            value: e.value,
            label: e.label,
            children: []
          })
          e.children.forEach(n => {
            if ('七年级，八年级，九年级'.includes(n.label)) {
              this.courseCascadeList[i].children.push(n)
            }
          })
        })
        console.log(this.courseCascadeList)
        this.form.termId = this.courseCascadeList[0].value
        this.form.gradeId = this.courseCascadeList[0].children[0].value
        this.form.examBatchId = this.courseCascadeList[0].children[0].children[0].value
        this.batchModel = [this.form.termId, this.form.gradeId, this.form.examBatchId]
      },
      // 获取课表
      async getDetail() {
        let query = JSON.parse(JSON.stringify(this.form))
        this.tableData = null
        query.analysisType = null
        let res = await this.$ajax({
          url: this.searchUrl,
          el: '.table-box',
          query,
        })
        this.tableData = this.$spliceHTML(res)
      },
      // 分析表类型切换的回调
      changeAnalysisType() {
        switch (this.form.analysisType) {
          case 'single':
            this.searchUrl = '/score/queryJuniorSingleCourseStatData.do'
            this.form.courseId = this.courseList[0].id
            break
          case 'levelA':
            this.searchUrl = '/score/queryJuniorCourseLevelAStatData.do'
            this.form.courseId = null
            break
          case 'levelB':
            this.searchUrl = '/score/queryJuniorCourseLevelBConvertStatData.do'
            this.form.courseId = null
            break
          case 'whole':
            this.form.courseId = null
            this.searchUrl = '/score/queryJuniorClassEntireStatData.do'
            break
        }
        this.getDetail()
      },
      // 考试批次的切换
      async changeBatch() {
        this.form.termId = this.batchModel[0]
        this.form.gradeId = this.batchModel[1]
        this.form.examBatchId = this.batchModel[2]
        this.getDetail()
      },
      // 导入文化成绩
      async impFun() {
        if (!this.impBatchList.length) {
          this.$message.warning('请选择考试批次')
          return
        }
        let file = this.$refs.upload.$refs.input.files[0]
        if (!file) {
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('termId', this.impBatchList[0])
        formData.append('gradeId', this.impBatchList[1])
        formData.append('examBatchId', this.impBatchList[2])
        let obj = {
          url: '/import/importScore.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        if (this.dialogTitle === '导入非文化成绩') {
          obj.url = '/import/importScoreNonCultural.do'
        }
        // // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if (res.errMsgList && res.errMsgList.length) {
          this.$message.error(res.errMsgList[0])
        } else {
          this.$remind('导入成功')
          this.dialog = false
          this.getDetail()
        }
      },
    },
    async mounted() {
      await this.getCourseCascade()
      await this.getSubject()
      this.getDetail()

    },
    computed:{
      'impForm': function () {
        let form = JSON.parse(JSON.stringify(this.form))
        form.analysisType = undefined
        return form
      }
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .btn-oper
      position: absolute
      top: 29px
      right: 15px
      .btn-submit
        height: 40px
        line-height: 40px
    .search-box
      margin-top: 10px
      .item
        display: inline-block
        margin-right: 15px
      .text
        margin-left: 5px
        color: $main-c
        display: block
        margin-bottom: 3px
      .cascader
        width: 320px
    .table-box
      margin-top: 20px
      width: calc(100% - 15px)
      /deep/ table
        width: 100%
        .r1:nth-child(1),.r1:nth-child(2)
          background: $back-dark
          td
            color: $fc-def
            font-size: $font-normal
      .no-data
        color: $gray-dark
        text-align: center
        line-height: 100px
    .dialog
      .title
        width: 80px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .input
        width: calc(100% - 140px)
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .file-input
        /deep/ input
          padding-top: 10px
          line-height: 1
      .item
        margin-bottom: 20px
      .btns
        text-align: center

</style>
