<template>
  <div class="index">
    <div class="btn-oper">
      <div @click="showDialog('culture')" class="btn-submit mr15">导入文化成绩</div>
      <div @click="showDialog('level')" class="btn-submit mr15">导入得分层次数据</div>
      <div v-show="form.analysisType !== 'SCORE_LEVEL_ANALYSIS'" @click="$exportDetail('/export/exportSeniorSingleCourseData.do', form)" class="btn-submit">导出</div>
      <div v-show="form.analysisType === 'SCORE_LEVEL_ANALYSIS'" @click="$exportDetail('/export/exportSeniorScoreStageData.do', form)" class="btn-submit">导出</div>
    </div>
    <div class="search-box">
      <div class="item">
        <span class="text">分析表类型</span>
        <el-select @change="changeAnalysisType()" v-model="form.analysisType">
          <el-option v-for="item in $enums.SeniorScoreAnalysisType.list" :key="item.value"
                     :label="item.label" :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="item">
        <span class="text">考试批次</span>
        <el-cascader @change="changeBatch" class="cascader" v-model="batchModel" :options="courseCascadeList"></el-cascader>
      </div>
      <div class="item" v-show="form.analysisType !== 'TOTAL_SCORE_ANALYSIS'">
        <el-select @change="getDetail()" default-first-option filterable v-model="form.courseId">
          <el-option v-for="item in courseList" :key="item.id"
                     :label="item.name" :value="item.id"
          ></el-option>
        </el-select>
      </div>
    </div>
    <div class="table-box">
      <div v-show="tableData" v-html="tableData"></div>
      <div v-show="!tableData" class="no-data">暂无数据</div>
    </div>
    <el-dialog :title="dialogTitle" class="dialog" :visible.sync="dialog" width="540px">
      <div v-show="dialogTitle === '导入得分层次数据'" class="item">
        <span class="title imp">科目</span>
          <el-select class="input" default-first-option filterable v-model="impCourse">
            <el-option v-for="item in courseList" :key="item.id"
                       :label="item.name" :value="item.id"
            ></el-option>
        </el-select>
      </div>
      <div class="item">
        <span class="title imp">考试批次</span>
        <el-cascader
            class="input"
            clearable
            v-model="impBatchList"
            :options="courseCascadeList"
        ></el-cascader>
      </div>
      <div class="item">
        <span class="title imp">上传文件</span>
        <el-input type="file" class="input file-input" ref="upload"
                  accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                  placeholder="请选择"></el-input>
        <a v-show="dialogTitle === '导入文化数据'" download href="../gy-files/template/初高中文化成绩导入模板.xls" class="f-small main-c ml5">下载模板</a>
        <a v-show="dialogTitle === '导入得分层次数据'" download href="../gy-files/template/高中得分层次数据导入模板.xls" class="f-small main-c ml5">下载模板</a>
      </div>
      <div class="btns">
        <div @click="impFun()" class="btn-submit btn-upload mr15">确认</div>
        <div @click="dialog = false" class="btn-clear">取消</div>
      </div>
    </el-dialog>
  </div>

</template>
<script>
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail,mixins.$spliceHTML],
    data() {
      return {
        // 科目列表
        courseList: [],
        // 选择的考试批次
        batchModel: [],
        // 查询条件
        form:{
          list:[],
          termId: null,
          gradeId: null,
          examBatchId: null,
          courseId: null,
          analysisType: this.$enums.SeniorScoreAnalysisType.list[0].value,
        },
        // 表格数据
        tableData: '',
        dialog: false,
        // 导入时选择的考试批次
        impBatchList: [],
        // 导入时选择的科目
        impCourse: null,
        // 学期级联列表
        courseCascadeList: [],
        dialogTitle: ''
      }
    },
    methods: {
      // 展现弹窗
      showDialog(type){
        this.dialogTitle = type === 'level'? '导入得分层次数据' : '导入文化数据'
        this.dialog = true
      },
      // 获取学科
      async getSubject(){
        this.courseList = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do',
        })
      },
      // 获取学期 年级 考试批次级联
      async getCourseCascade() {
        this.courseCascadeList = await this.$ajax({
          url: '/score/queryTermGradeExamCascade.do'
        })
        let arr = this.courseCascadeList
        this.courseCascadeList = []
        arr.forEach((e, i) => {
          this.courseCascadeList.push({
            value: e.value,
            label: e.label,
            children: []
          })
          e.children.forEach(n => {
            if ('高一，高二，高三'.includes(n.label)) {
              this.courseCascadeList[i].children.push(n)
            }
          })
        })
        this.form.termId = this.courseCascadeList[0].value
        this.form.gradeId = this.courseCascadeList[0].children[0].value
        if(this.courseCascadeList[0].children[0].children[0]){
          this.form.examBatchId = this.courseCascadeList[0].children[0].children[0].value
        }
        this.batchModel = [this.form.termId, this.form.gradeId, this.form.examBatchId]
      },
      // 获取详情
      async getDetail(){
        let url = '/score/querySeniorScoreStageDataHtml.do'
        if(this.form.analysisType !== 'SCORE_LEVEL_ANALYSIS'){
          url = '/score/querySeniorSingleCourseDataHtml.do'
        }
        this.tableData = ''
        let res = await this.$ajax({
          url,
          el: '.table-box',
          query: this.form
        })
        this.tableData = this.$spliceHTML(res)
      },
      // 分析表类型切换的回调
      changeAnalysisType(){
        if(this.form.analysisType === 'SINGLE_COURSE_ANALYSIS'){
          this.form.courseId = this.courseList[0].id
        } else if(this.form.analysisType === 'SCORE_LEVEL_ANALYSIS'){
          this.form.courseId = this.courseList[0].id
        } else{
          this.form.courseId = null
        }
        this.getDetail()
      },
      // 考试批次的切换
      async changeBatch(){
        this.form.termId = this.batchModel[0]
        this.form.gradeId = this.batchModel[1]
        this.form.examBatchId = this.batchModel[2]
        this.getDetail()
      },
      // 导入
      async impFun() {
        if(!this.impBatchList.length){
          this.$message.warning('请选择考试批次')
          return
        }
        if(this.dialogTitle === '导入得分层次数据' && !this.impCourse){
          this.$message.warning('请选择科目')
          return
        }
        let file = this.$refs.upload.$refs.input.files[0]
        if(!file){
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('termId', this.impBatchList[0])
        formData.append('gradeId', this.impBatchList[1])
        formData.append('examBatchId', this.impBatchList[2])
        let obj = {
          url: '/import/importScore.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        if(this.dialogTitle === '导入得分层次数据'){
          obj.url = '/import/importScoreStage.do'
          formData.append('courseId', this.impCourse)
        }
        // // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.$remind('导入成功')
        this.dialog = false
        this.getDetail()
      },
    },
    async mounted(){
      await this.getCourseCascade()
      this.getDetail()
      this.getSubject()
    },
    computed:{
      'impForm': function () {
        let form = JSON.parse(JSON.stringify(this.form))
        form.analysisType = undefined
        return form
      }
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .btn-oper
      position: absolute
      top: 29px
      right: 15px
      .btn-submit
        height: 40px
        line-height: 40px
    .search-box
      margin-top: 10px
      .item
        display: inline-block
        margin-right: 15px
      .text
        margin-left: 5px
        color: $main-c
        display: block
        margin-bottom: 3px
      .cascader
        width: 280px
    .table-box
      margin-top: 20px
      min-width: 1100px
      overflow-x: auto
      width: calc(100% - 15px)
      /deep/ table
        width: 100%
        .r1:nth-child(1),.r1:nth-child(2)
          background: $back-dark
          td
            color: $fc-def
            font-size: $font-normal
      .no-data
        line-height: 100px
        text-align: center
        color: $gray-dark
    .dialog
      .title
        width: 80px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .input
        width: calc(100% - 140px)
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .file-input
        /deep/ input
          padding-top: 10px
          line-height: 1
      .item
        margin-bottom: 20px
      .btns
        text-align: center

</style>
